rch]::-webkit-search-results-button,\r\ninput[type=search]::-webkit-search-results-decoration {\r\n  -webkit-appearance: none;\r\n  -moz-appearance: none;\r\n}\r\n\r\ninput[type=search] {\r\n  -webkit-appearance: none;\r\n  -moz-appearance: none;\r\n  -webkit-box-sizing: content-box;\r\n  -moz-box-sizing: content-box;\r\n  box-sizing: content-box;\r\n}\r\n\r\ntextarea {\r\n  overflow: auto;\r\n  vertical-align: top;\r\n  resize: vertical;\r\n}\r\n\r\n/**\r\n * Correct `inline-block` display not defined in IE 6/7/8/9 and Firefox 3.\r\n */\r\n\r\naudio,\r\ncanvas,\r\nvideo {\r\n  display: inline-block;\r\n  *display: inline;\r\n  *zoom: 1;\r\n  max-width: 100%;\r\n}\r\n\r\n/**\r\n * Prevent modern browsers from displaying `audio` without controls.\r\n * Remove excess height in iOS 5 devices.\r\n */\r\n\r\naudio:not([controls]) {\r\n  display: none;\r\n  height: 0;\r\n}\r\n\r\n/**\r\n * Address styling not present in IE 7/8/9, Firefox 3, and Safari 4.\r\n * Known issue: no IE 6 support.\r\n */\r\n\r\n[hidden] {\r\n  display: none;\r\n}\r\n\r\n/**\r\n * 1. Correct text resizing oddly in IE 6/7 when body `font-size` is set using\r\n *    `em` units.\r\n * 2. Prevent iOS text size adjust after orientation change, without disabling\r\n *    user zoom.\r\n */\r\n\r\nhtml {\r\n  font-size: 100%; /* 1 */\r\n  -webkit-text-size-adjust: 100%; /* 2 */\r\n  -ms-text-size-adjust: 100%; /* 2 */\r\n}\r\n\r\n/**\r\n * Address `outline` inconsistency between Chrome and other browsers.\r\n */\r\n\r\na:focus {\r\n  outline: thin dotted;\r\n}\r\n\r\n/**\r\n * Improve readability when focused and also mouse hovered in all browsers.\r\n */\r\n\r\na:active,\r\na:hover {\r\n  outline: 0;\r\n}\r\n\r\n/**\r\n * 1. Remove border when inside `a` element in IE 6/7/8/9 and Firefox 3.\r\n * 2. Improve image quality when scaled in IE 7.\r\n */\r\n\r\nimg {\r\n  border: 0; /* 1 */\r\n  -ms-interpolation-mode: bicubic; /* 2 */\r\n}\r\n\r\n/**\r\n * Address margin not present in IE 6/7/8/9, Safari 5, and Opera 11.\r\n */\r\n\r\nfigure {\r\n  margin: 0;\r\n}\r\n\r\n/**\r\n * Correct margin displayed oddly in IE 6/7.\r\n */\r\n\r\nform {\r\n  margin: 0;\r\n}\r\n\r\n/**\r\n * Define consistent border, margin, and padding.\r\n */\r\n\r\nfieldset {\r\n  border: 1px solid #c0c0c0;\r\n  margin: 0 2px;\r\n  padding: 0.35em 0.625em 0.75em;\r\n}\r\n\r\n/**\r\n * 1. Correct color not being inherited in IE 6/7/8/9.\r\n * 2. Correct text not wrapping in Firefox 3.\r\n * 3. Correct alignment displayed oddly in IE 6/7.\r\n */\r\n\r\nlegend {\r\n  border: 0; /* 1 */\r\n  padding: 0;\r\n  white-space: normal; /* 2 */\r\n  *margin-left: -7px; /* 3 */\r\n}\r\n\r\n/**\r\n * 1. Correct font size not being inherited in all browsers.\r\n * 2. Address margins set differently in IE 6/7, Firefox 3+, Safari 5,\r\n *    and Chrome.\r\n * 3. Improve appearance and consistency in all browsers.\r\n */\r\n\r\nbutton,\r\ninput,\r\nselect,\r\ntextarea {\r\n  font-size: 100%; /* 1 */\r\n  margin: 0; /* 2 */\r\n  vertical-align: baseline; /* 3 */\r\n  *vertical-align: middle; /* 3 */\r\n}\r\n\r\n/**\r\n * Address Firefox 3+ setting `line-height` on `input` using `!important` in\r\n * the UA stylesheet.\r\n */\r\n\r\nbutton,\r\ninput {\r\n  line-height: normal;\r\n}\r\n\r\n/**\r\n * Address inconsistent `text-transform` inheritance for `button` and `select`.\r\n * All other form control elements do not inherit `text-transform` values.\r\n * Correct `button` style inheritance in Chrome, Safari 5+, and IE 6+.\r\n * Correct `select` style inheritance in Firefox 4+ and Opera.\r\n */\r\n\r\nbutton,\r\nselect {\r\n  text-transform: none;\r\n}\r\n\r\n/**\r\n * 1. Avoid the WebKit bug in Android 4.0.* where (2) destroys native `audio`\r\n *    and `video` controls.\r\n * 2. Correct inability to style clickable `input` types in iOS.\r\n * 3. Improve usability and consistency of cursor style between image-type\r\n *    `input` and others.\r\n * 4. Remove inner spacing in IE 7 without affecting normal text inputs.\r\n *    Known issue: inner spacing remains in IE 6.\r\n */\r\n\r\nbutton,\r\nhtml input[type=\"button\"], /* 1 */\r\ninput[type=\"reset\"],\r\ninput[type=\"submit\"] {\r\n  -webkit-appearance: button; /* 2 */\r\n  cursor: pointer; /* 3 */\r\n  *overflow: visible;  /* 4 */\r\n}\r\n\r\n/**\r\n * Re-set default cursor for disabled elements.\r\n */\r\n\r\nbutton[disabled],\r\nhtml input[disabled] {\r\n  cursor: default;\r\n}\r\n\r\n/**\r\n * 1. Address box sizing set to content-box in IE 8/9.\r\n * 2. Remove excess padding in IE 8/9.\r\n * 3. Remove excess padding in IE 7.\r\n *    Known issue: excess padding remains in IE 6.\r\n */\r\n\r\ninput[type=\"checkbox\"],\r\ninput[type=\"radio\"] {\r\n  box-sizing: border-box; /* 1 */\r\n  padding: 0; /* 2 */\r\n  *height: 13px; /* 3 */\r\n  *width: 13px; /* 3 */\r\n}\r\n\r\n/**\r\n * 1. Address `appearance` set to `searchfield` in Safari 5 and Chrome.\r\n * 2. Address `box-sizing` set to `border-box` in Safari 5 and Chrome\r\n *    (include `-moz` to future-proof).\r\n */\r\n\r\ninput[type=\"search\"] {\r\n  -webkit-appearance: textfield; /* 1 */\r\n  -moz-box-sizing: content-box;\r\n  -webkit-box-sizing: content-box; /* 2 */\r\n  box-sizing: content-box;\r\n}\r\n\r\n/**\r\n * Remove inner padding and search cancel button in Safari 5 and Chrome\r\n * on OS X.\r\n */\r\n\r\ninput[type=\"search\"]::-webkit-search-cancel-button,\r\ninput[type=\"search\"]::-webkit-search-decoration {\r\n  -webkit-appearance: none;\r\n}\r\n\r\n/**\r\n * Remove inner padding and border in Firefox 3+.\r\n */\r\n\r\nbutton::-moz-focus-inner,\r\ninput::-moz-focus-inner {\r\n  border: 0;\r\n  padding: 0;\r\n}\r\n\r\n/**\r\n * 1. Remove default vertical scrollbar in IE 6/7/8/9.\r\n * 2. Improve readability and alignment in all browsers.\r\n */\r\n\r\ntextarea {\r\n  overflow: auto; /* 1 */\r\n  vertical-align: top; /* 2 */\r\n}\r\n\r\n/**\r\n * Remove most spacing between table cells.\r\n */\r\n\r\ntable {\r\n  border-collapse: collapse;\r\n  border-spacing: 0;\r\n}\r\n\r\nhtml,\r\nbutton,\r\ninput,\r\nselect,\r\ntextarea {\r\n  color: #222;\r\n}\r\n\r\n\r\n::-moz-selection {\r\n  background: #b3d4fc;\r\n  text-shadow: none;\r\n}\r\n\r\n::selection {\r\n  background: #b3d4fc;\r\n  text-shadow: none;\r\n}\r\n\r\nimg {\r\n  vertical-align: middle;\r\n}\r\n\r\nfieldset {\r\n  border: 0;\r\n  margin: 0;\r\n  padding: 0;\r\n}\r\n\r\ntextarea {\r\n  resize: vertical;\r\n}\r\n\r\n.chromeframe {\r\n  margin: 0.2em 0;\r\n  background: #ccc;\r\n  color: #000;\r\n  padding: 0.2em 0;\r\n}","$columns: 12;\r\n$columns-base-width: 100% / $columns;\r\n\r\n.container {\r\n  max-width: 1140px;\r\n  margin: 0 auto;\r\n  width: 100%;\r\n  padding: 0 20px;\r\n\r\n  .row {\r\n    display: flex;\r\n    flex-wrap: wrap;\r\n    [class*='col-'] {\r\n      min-height: 1px;\r\n      width: $columns-base-width;\r\n    }\r\n\r\n    // Mobile size max width max-width 767 px\r\n\r\n    @for $i from 1 through $columns {\r\n      .col-#{$i} {\r\n        width: $columns-base-width * $i;\r\n      }\r\n    }\r\n\r\n    // tablet size  //min-width: 768 px\r\n    @for $i from 1 through $columns {\r\n      @include tablet {\r\n        .col-md-#{$i} {\r\n          width: $columns-base-width * $i;\r\n        }\r\n      }\r\n    }\r\n\r\n    // desktop size //min-width: 1200px\r\n    @for $i from 1 through $columns {\r\n      @include desktop {\r\n        .col-xl-#{$i} {\r\n          width: $columns-base-width * $i;\r\n        }\r\n      }\r\n    }\r\n  }\r\n}","//tablet size\r\n\r\n@mixin tablet {\r\n  @media (min-width: 768px) {\r\n    @content;\r\n  }\r\n}\r\n\r\n//desktop size\r\n\r\n@mixin desktop {\r\n  @media (min-width: 1200px) {\r\n    @content;\r\n\r\n  }\r\n}\r\n","@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;700&display=swap');\r\n\r\nhtml {\r\n  font-size: 10px;\r\n  scroll-behavior: smooth;\r\n  font-family: 'Inter', sans-serif;\r\n}\r\n\r\n.hide {\r\n  display: none;\r\n}\r\n\r\n",".loadingScreen {\r\n  background-color: $loading-screen-backgroundColor;\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  height: 100vh;\r\n\r\n  &__title {\r\n    width: 350px;\r\n    line-height: 50px;\r\n    text-align: center;\r\n    color: $loading-screen-title-color;\r\n    font-size: 3rem;\r\n    position: relative;\r\n    animation-name: fadeOut;\r\n    animation-duration: 3s;\r\n    animation-iteration-count: 1;\r\n    animation-timing-function: ease-out;\r\n    animation-fill-mode: forwards;\r\n\r\n    &::before {\r\n      top: 0;\r\n    }\r\n    &::after {\r\n      bottom: 0;\r\n    }\r\n\r\n    &::before, &::after {\r\n      position: absolute;\r\n      content: '';\r\n      display: block;\r\n      width: 15px;\r\n      height: 30px;\r\n      background-color: $loading-screen-squares;\r\n      animation-name: squaresMove;\r\n      animation-duration: 0.8s;\r\n      animation-timing-function: linear;\r\n      animation-iteration-count: infinite;\r\n      animation-direction: alternate;\r\n    }\r\n\r\n    @keyframes squaresMove {\r\n      0% {\r\n        height: 30px;\r\n        width: 15px;\r\n        left: 0;\r\n      }\r\n      50% {\r\n        height: 8px;\r\n        width: 50px;\r\n      }\r\n      100% {\r\n        left: 335px;\r\n        height: 30px;\r\n        width: 15px;\r\n      }\r\n    }\r\n    @keyframes fadeOut {\r\n      0% {\r\n        opacity: 1;\r\n      }\r\n      100% {\r\n        opacity: 0;\r\n      }\r\n\r\n    }\r\n\r\n  }\r\n}","// loading screen\r\n\r\n$loading-screen-backgroundColor: #151515;\r\n$loading-screen-squares: rgba(233, 74, 69, .5);\r\n$loading-screen-title-color: lightgray;\r\n\r\n//mobile navigation\r\n\r\n$navigation-list-background: #151515;\r\n$navigation-hamburger-color: #cccccc;\r\n$navigation-list-element-color: #fff;\r\n$navigation-list-element-backgroundColor: rgba(16, 172, 132, 0.7);\r\n\r\n\r\n\r\n\r\n\r\n//box-shadow\r\n\r\n$projects-box-shadow: 0 1rem 1.5rem rgba(0,0,0,0.3);",".mobile-header {\r\n  background: transparent;\r\n  border: 1px solid $navigation-list-background;\r\n  display: flex;\r\n  justify-content: space-between;\r\n  position: absolute;\r\n  width: 100%;\r\n  @include desktop {\r\n    display: none;\r\n  }\r\n\r\n\r\n\r\n  .mobile-logo {\r\n    color: lightgray;\r\n    display: block;\r\n    font-size: 3rem;\r\n    padding: 10px;\r\n  }\r\n\r\n  .hamburger-container {\r\n    z-index: 3;\r\n    position: relative;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    cursor: pointer;\r\n    transition: all .5s ease-in-out;\r\n\r\n    .hamburger {\r\n      width: 50px;\r\n      height: 6px;\r\n      background: #fff;\r\n      border-radius: 5px;\r\n      box-shadow: 0 2px 5px rgba(255,101,47,.2);\r\n      transition: all .5s ease-in-out;\r\n      position: relative;\r\n      &::before, &::after {\r\n        content: '';\r\n        position: absolute;\r\n        width: 50px;\r\n        height: 6px;\r\n        background: #fff;\r\n        border-radius: 5px;\r\n        box-shadow: 0 2px 5px rgba(255,101,47,.2);\r\n        transition: all .5s ease-in-out;\r\n      }\r\n      &::before {\r\n        transform: translateY(-16px);\r\n      }\r\n\r\n      &::after {\r\n        transform: translateY(16px);\r\n      }\r\n    }\r\n\r\n  }\r\n\r\n\r\n\r\n\r\n\r\n  // animation\r\n\r\n  .hamburger-container.open {\r\n\r\n    .hamburger {\r\n      transform: translateX(-50px);\r\n      background: transparent;\r\n      box-shadow: none;\r\n\r\n      &::before {\r\n        transform: rotate(45deg) translate(35px, -35px);\r\n      }\r\n      &::after {\r\n        transform: rotate(-45deg) translate(35px, 35px);\r\n      }\r\n    }\r\n  }\r\n\r\n  .mobile-navigation {\r\n    z-index: 1;\r\n    position: absolute;\r\n    border-top: 3px solid $navigation-list-background;\r\n    border-right: 3px solid $navigation-list-background;\r\n    border-bottom: 3px solid $navigation-list-background;\r\n    background-color: rgba(0,0,0